.Chef{
    width: 300px;
    background-color: rgb(233, 235, 234);
    box-shadow: 2px 2px 5px rgb(122, 122, 122);
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 9px;
    position: relative;
    cursor: pointer;
    transition: all 0.3s linear;
    .ChefImg{
        width: 100%;
        height: 400px;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            transition: all 0.3s linear;
        }
    }
    .ChefInfo, .ChefSocialMedia{
        background-color: rgba(0, 0, 0, 0.404);
        width: 100%;
        height: 70px;
        border-radius: 0 0 9px 9px;
        position: absolute;
        bottom: 0;
        left: 0;
    }
    .ChefInfo{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: absolute;
        bottom: 0;
        left: 0;
        h3{
            color: white;
        }
        p{
            color: gold;
        }
    }
    .ChefSocialMedia{
        display: none;
        .SocialMedia_List{
            height: 70px;
            a{
                img{
                    height: 40px;
                    width: 40px;
                }
            }
        }
    }
}
.Chef:hover{
    img{
        transform: scale(1.1);
    }
    .ChefInfo{
        display: none;
    }
    .ChefSocialMedia{
        display: block;
    }
}

