.Logo{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px;
    gap: 5px;
    cursor: pointer;    
    img{
        height: 60px;
        width: 60px;
    }
    h1{
        font-size: 30px;
        color: var(--color);
    }
}

.Logo:hover{
    h1{
        color: var(--main-hover);
    }
    img{
        animation: LogoImage 4s linear infinite;
    }
}

@keyframes LogoImage {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}